html, body {
	height: 100%;
	margin: 0;
	padding: 0;
  }
  
  .mode-container {
	position: relative;
	height: 100vh; /* Ensure the container takes the full viewport height */
	width: 100%;
	overflow: hidden;
	background-color: #13161e;
  }
  
  .mode-background {
	position: absolute;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	display: flex;
	justify-content: center;
	align-items: center;
	transition: transform 1s ease; /* Smooth transition */
  }
  
  .mode-background.slide-left {
	transform: translateX(-50%);
  }

  .mode-background.left {
	transform: translateX(-50%);
  }

  .mode-background.slide-right {
	transform: translateX(50%);
  }

  .mode-background.right {
	transform: translateX(50%);
  }

  .mode-background-image {
	position: absolute;
	top: 0;
	left: 50%;
	transform: translateX(-50%);

	height: 100%;
	background-size: contain;
	background-position: center;
	background-repeat: no-repeat;
  }
  
  .mode-component {
	position: relative;
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: center;
	height: 100%;
	color: #fff;
	text-align: center;
  }

	.state-main-menu {
		height: 50%;
		width: 50%;
		background-color: black;
	}
  