	background-color: red;
	color: red;
	.next-button {
		position: absolute;
		left: 0px;
		height: 100px;
		width: 100px;
		font-size: 16px;
		color: grey;
	}
	.accept-button {
		position: absolute;
		left: 0px;
		bottom: 0px;
		height: 100px;
		width: 100px;
