.main-menu-container {
	position: relative;
	height: 100vh;
	width: 100%;
	overflow: hidden;
}

.main-menu-content {
	position: relative;
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: center;
	height: 100%;
	color: #13161e;
	text-align: center;
}

.main-menu-title {
	font-size: 2.5em;
	margin-bottom: 20px;
	color: white;
}

.main-menu-options {
	display: flex;
	flex-direction: column;
	gap: 10px;
	margin-bottom: 20px;

}

.main-menu-options button {
	padding: 10px 20px;
	font-size: 1.2em;
	cursor: pointer;
	border: none;
	border-radius: 5px;
	background-color: rgba(20, 0, 4, 0.7);
	color: #fff;
	transition: background-color 0.3s;

	&.disabled {
		cursor: default;
		color: gray;
	}
}

.main-menu-options button:hover {
	background-color: rgba(100, 0, 50, 0.9);

	&.disabled {
		background-color: rgba(0, 0, 0, 0.7);
	}
}

.exit-button {
	padding: 10px 20px;
	font-size: 1.2em;
	cursor: pointer;
	border: none;
	border-radius: 5px;
	background-color: rgba(255, 0, 0, 0.7);
	color: #fff;
	transition: background-color 0.3s;
}

.exit-button:hover {
	background-color: rgba(255, 0, 0, 0.9);
}